<template>
	<div class="box">
		<div class="search_box">
			<span>
				<input type="text" class="search" placeholder="搜影院"
					v-model="message" 
					@keyup="search()"/>
			</span>
			<span @click="goback">
				取消
			</span>
		</div>
		<div class="content">
			<div class="Item" v-for="item,index in list" @click="Tdetail(item.id)">
				<p>
					<span>{{item.nm}}</span>
					<span style="color: red; font-size: 20px;">{{item.sellPrice}}</span>
					<span>元起</span>
				</p>
				<p style="height: 17px;">
					<span>{{item.addr}}</span>
					<span>{{item.distance}}</span>
				</p>
				<p>{{item.vipDesc}}</p>
			</div>
		</div>
	</div>
</template>

<script>
//	http://m.maoyan.com/ajax/search?kw=%E5%A4%A7di&cityId=10&stype=2
//	http://m.maoyan.com/ajax/search?kw=%E5%A4%A7%E5%BC%9F&cityId=10&stype=2
//	http://m.maoyan.com/ajax/search?kw=%E5%A4%A7&cityId=10&stype=2
	export default{
		created(){
			
		},
		data(){
			return {
				message:"",
				list:[]
			}
		},
		watch:{
			message(){
				this.search()
			}
		},
		methods:{
			search(){
				let url="/my/ajax/search?kw="+this.message+"&cityId=10&stype=2"
				this.$http.get(url).then(res=>{
//					var data = {};
//					var arr = Object.keys(data);
//					alert(arr.length == 0);//true
					
					var arr=Object.keys(res.data)
					console.log(res)
					if(arr.length!=0){
						this.list=res.data.cinemas.list
					}else{
						this.list={}
					}
				})
			},
			Tdetail(index){
				this.$router.push("/Tdetail/"+index)
			},
			goback(){
				this.$router.push("/cinema")
			}
		}
	}
</script>

<style scoped>
	.box{
		width: 100%;
		height: 47px;
		background: #f5f5f5;;
	}
	.box .search_box{
		width: 100%;
		height: 47px;
		box-sizing: border-box;
		padding: 8px 0 8px 10px;
	}
	.box .search_box span{
		float: left
	}
	.box .search_box span:nth-child(1){
		display: block;
		width: 85%;
		height: 100%;
	}
	.box .search_box span:nth-child(1) input{
		
		width: 100%;
		height: 100%;
		padding-left:5px ;
		border: none;
		background: #fff;
		border-radius: 5px;
		font-size: 16px;
		outline:none
	}
	.box .search_box span:nth-child(2){
		display: block;
		width: 15%;
		height: 100%;
		line-height: 30px;
		padding: 0 10px;
	}
	/*搜索显示内容*/
	.content{
		width: 100%;
		
		position: absolute;
		top:50px;
		padding-bottom:49px ;
	}
	.box .content .Item {
		width: 100%;
		
		height: 102px;
		box-sizing: border-box;
		padding: 15px;
	}
	.box .content p{
		width: 100%;
		display: flex;
		justify-content: space-start;
	}
	.box .content .Item p span{
		display: block;
	}
	.box .content .Item p:nth-child(2) span:nth-child(1){
		width: 283px;
		height: 20px;
		line-height: 20px;
		white-space: nowrap;
	    text-overflow: ellipsis;
	    overflow: hidden;
	}
	/*.box .content .Item {
		width: 100%;
		display: flex;
		justify-content:space-around
	}*/
	/*.content .Item span{
		display: block;
		height: 30px;
		line-height: 30px;
	}
	.content .Item span:nth-child(1){
		width: 50px;
	}
	.content .Item span:nth-child(2){
		width: 250px;
	}
	.content .Item span:nth-child(1){
		width: 50px;
	}*/
</style>


